.interface {
  width: 100%;
}
.home {
  width: 100vw;
  min-height: 100vh;
  display: grid;
  grid-template-columns: 1fr 3fr;
  column-gap: 20px;
  background-color: rgb(240, 242, 245);
  padding: 0px 10px;
  position: relative;
}
